.module-solutions {
	&.article-show-page {

		.page-title {
			min-height: 32px;

			.btn-send-for-approval, .btn-article-publish, .article-actions, .copy-btn, .article-approval-actions {
				margin-left: auto;
			}

			.btn-article-publish, .btn-send-for-approval, .article-approval-actions, .copy-btn {
				& + .article-actions {
					margin-left: inherit !important;
				}
			}

			.article-actions {
				.ember-basic-dropdown-content {
					padding: 8px 12px;
					min-width: 100px;
				}
			}

			.article-approval-actions {
				margin-right: 8px;
				.ember-basic-dropdown-content {
					min-width: 160px;
				}
			}

			/* fix caused due to div:last-child; */
			.ember-breadcrumbs {
				margin-left: inherit !important;
			}
		}

		.page-content {
			.view-on-portal {
				margin: 8px;
				.remove-alert-divider {
					&:before {
						content: none;
					}
				}
				.current-language {
					display: flex;
					align-items: center;
					margin-right: 8px;

					.language-flags {
						margin-right: 4px;
						border-radius: 4px;
					}
				}
				.article-actions{
					margin-left: auto;
					> button {
						font-size: $title-small;
						border: 0;
						outline: none;
					}
					> a {
						margin-left: 16px;
					}
				}
				
			}

			.update-outdated {
				margin: 24px 32px 0;
				background-color: $app-casablanca-light;

				> button {
					color: $app-error-state !important;
					font-size: $title-small;
					border: 0;
					margin-left: auto;
					outline: none;
				}
			}

			.article-title {
				margin: 24px 32px 0;
				font-size: 20px;
				font-weight: 600;
				width: calc(100% - 64px);
				word-break: break-word;
				line-height: 1.4;
			}

			.article-content {
				margin: 32px;
				padding-top: 8px;
				overflow: auto;
				ul{
					list-style: initial;
				}
			}

			.article-attachments{
				background-color: $app-grey-bg;
				width: calc(100% - 32px);
				margin: 0 auto;

				.attach-files{
					padding: 16px 0;
				}
			}
		}

		.page-sidebar {
			overflow: inherit;
			.article-sidebar-properties {
				>.article-properties:first-child {
					border-bottom: 1px solid $app-border-primary;
					margin-bottom: 0;
					padding: 24px 16px 8px;
				}

				.article-properties {
					box-sizing: border-box;
					padding: 8px 16px 0;
					margin-bottom: 16px;

					&>div:first-child {
						padding-bottom: 8px;
					}

					div:last-child {

						.article-created-date,
						.article-modified-date {
							font-size: 12px;
							padding-bottom: 8px;

							span:first-child {
								color: $secondary-text-color-1;
								display: block;
							}

							span:last-child {
								font-weight: 500;
								word-break: break-word;
							}
						}
					}

					.article-category,
					.article-folder {
						.article-property-value {
							width: 260px;
							display: inline-block;
							font-weight: 600;
							font-size: 14px;
						}
					}
				}

				.available-languages{
					box-sizing: border-box;
					padding: 8px 8px 0;
				}

				.article-properties .article-approvers .article-approver {
					margin-bottom: 8px;
					& > div {
						display: flex;
						align-items: center;

						.ember-svg {
							padding-right: 8px;
							width: 23px;
							height: 23px;
							&.requested {
								fill: $mild-text;
							}
							&.approved {
								fill: $app-success-state;
								/* legacy override */
								background: none;
							}
							&.rejected, &.peer_responded {
								fill: $app-warning-state;
								/* legacy override */
								background: none;
							}
						}

						span {
							font-weight: $text-weight-medium;
						}
					}
					& > span {
						padding-left: 24px;
						color: $secondary-text-color-1;
						font-size: $text-normal;
					}

					& > a {
						cursor: pointer;
					}

					.approver-name {
						display: inline-block;
						max-width: 200px;
						@extend .ellipsis;
					}
				}

				.article-analytics .ui-accordion-panel-content .article-properties {
					margin-bottom: 5px;
					>div {
						.article-property-value {
							margin-left: auto;
							font-weight: 500;
						}
					}
				}

				.article-languages .ui-accordion-panel-content .available-languages {
					margin-bottom: 5px;
					overflow-y: auto;
					max-height: 215px;
				}

				.outdate-article-container {
					margin: 14px 10px;

					.outdate-article {
						display: flex;
						align-items: center;
						font-weight: $text-weight-medium;

						.outdate-button {
							margin-left: auto;
							margin-right: 0;
							white-space: nowrap;
						}
					}
				}

				.article-reset-analytics {
					margin-top: 15px;
					display: inline-block;
					font-weight: 500;
					cursor: pointer;
				}

				.ui-accordion-item {
					.ui-accordion-header {
						box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
						padding-left: 16px;
						font-size: 16px;

						button {
							font-weight: 600;
							height: 40px;
							cursor: pointer;
							color: $primary-text-color-1;

							svg {
								width: 20px;
								height: 20px;
								float: right;
								margin-right: 8px;
							}

							&[aria-expanded] {
								svg {
									transform: rotate(180deg);
								}
							}
						}
					}

					.ui-accordion-panel-content {
						border-bottom: 1px solid $border-dark-color;

						.article-properties {
							>div {
								padding-bottom: 8px;
								border-bottom: 1px dashed $border-dark-color;
								margin-top: 8px;
								overflow-wrap: break-word;

								&:last-child {
									border: none;
									padding-bottom: 0;
								}
							}

							.article-property-label {
								font-size: 12px;
								color: $secondary-text-color-1;
								font-weight: 600;
							}
						}
						.available-languages {
							
							.current-language {
								background-color: $dropdown-option-selected;
							}
							.article-language-label {
								display: flex;
								justify-content: space-between;
								align-items: center;
								font-size: $baseFontSize;
								color: $secondary-text-color-1;
								font-weight: $text-weight-semi-bold;
								margin: 2px 0;
								padding: 4px;
								border-radius: 4px;

								.article-status {
									margin-left: auto;
								}

								.article-outdated {
									margin-left: 4px;
								}

								.translate-link {
									padding: 4px 0;
									font-size: $baseFontSize;
									color: $linkColor;
								}

								.translate-link:hover {
									text-decoration: underline;
								}

							}

							.article-language-label:hover{
								background-color: $background-color;
								cursor: pointer;
							}
						}
					}
				}
			}
		}
	}
}