.module-solutions {

	.category-list {
		/* category list specific adjustments */
		.category-item {

			border-bottom: 1px solid $border-light-color;

			.handle {
				margin-left: 4px;
			}
			.category-header {
				margin: 8px 4px 4px 8px;
			}
		}
	}

	/* category and folders list */
	.category-list, .folder-list {
		max-width: 296px;

		.dragSortItem {

			&.-placeholderAfter,
			&.-placeholderBefore {
				&:before {
					display: none;
				}
			}
		}

		.category-header, .folder-header {
			display: flex;
			height: 32px;
			line-height: 32px;
			cursor: pointer;
			border-radius: 4px;
			align-items: center;
			color: $primary-text-color-1;

			.category-title, .folder-title {
				width: 100%;
				font-weight: 500;
				/* ie fix */
				background-color: transparent;
				border: none;
				/* legacy class conflict */
				padding: 0;
			}

			/* icon */
			.category-open {
				margin-right: 4px;
				transition: all 0.2s ease;
				width: 20px;
				height: 20px;
				&.expanded{
					transform: rotate(180deg);
				}
			}

			svg {
				width: 16px;
				height: 16px;
				pointer-events: none;
			}

			.folder-icon {
				margin-left: 4px;
				margin-right: 8px;
				width: 18px;
				height: 18px;
				fill: $svg-icon-color;
			}

			.handle {
				margin-right: 2px;
				height: 12px;
				width: 12px;
				visibility: hidden;
			}

			&:hover{
				.handle{
					visibility: visible;
				}
			}

			&.active{

				background-color: $app-primary;
				color: $app-white;

				svg {
					fill: $app-white;
				}
			}

		}

		.folder-header {
			margin-left: 8px;
		}

		.-isDragging.-isDraggingOver {

			background-color: $background-color-2;

			.hint--right {
				display: none;
			}

			.category-header {
				margin-left: 4px;
				.category-title {
					margin-left: 16px;
				}
			}
		}

	}

	.folder-panel-content {
		padding: 4px;
		// height: 100%;
		background-color: $background-color-2;

		.no-folders-in-category {
			margin-bottom: 0;
			text-align: center;
			padding: 4px;
		}
	}

}
