.cue-wrapper {
	min-height: calc(100vh - 60px);
	background-color: $app-white;
	color: $app-grey;
	.knowledge-cue-keywords {
		display: flex;
		justify-content: space-between;
		padding: 32px;
		border-bottom: 1px solid $dropdown-hover;
		.left-section {
			width: 65%;
			h2 {
				font-size: $title-small;
				font-weight: $text-weight-semi-bold;
			}
			.cue-keywords {
				color: $alertSuccessColor;
				padding: 8px 0 25px;
				.keyword {
					font-size: $text-normal;
					display: inline-block;
					padding: 5px 10px;
					background-color: $app-jade-light;
					border: 1px solid $hummingBirdGreenBorder;
					margin: 0 7px 7px 0;
					border-radius: $radius;
					font-weight: $text-weight-semi-bold;
				}
			}
			.mentions-trending {
				display: flex;
				height: 20px;
				.mentions, .trending {
					display: flex;
					svg {
						width: 20px;
						margin-right: 7px;
					}
				}
				.mentions svg path{
					fill: $svg-orange;
				}
				.trending svg path{
					fill: $svg-green;
				}
			}

			.mentions {
				margin-right: 40px;
			}
		}
		.cue-modules-dropdown {
			.btn {
				margin: 0;
				display: flex;
				align-items: center;
				fill: $app-white;
				svg {
					width: 20px;
				}
			}
			.ember-basic-dropdown-content  {
				right: 0;
				font-weight: $text-weight-medium;
				width: 260px;
			}
			.ember-basic-dropdown-trigger[aria-expanded="true"] {
				transition: all 0.2s ease;
				.btn svg {
					transform: rotate(-180deg);
				}
			}
		}
		.mentions-trending {
			display: flex;
		}
		.cue-assigned {
			display: flex;
			color: $secondary-text-color-1;
			svg.cue-assigned-icon {
				width: 16px;
				height: 16px;
				fill: $secondary-text-color-1;
				margin-right: 6px;
			}
			h2 {
				font-size: $title-small;
			}
		}
	}
	.cue-helpful {
		padding: 16px 32px;
		border-bottom: 1px solid $dropdown-hover;
		display: flex;
		.left-section {
			margin-right: 15px;
			h2 {
				font-size: $title-small;
				font-weight: $text-weight-medium;
				line-height: 20px;
				padding-bottom: 2px;
			}
			span {
				line-height: 18px;
				font-size: $text-normal;
				color: $secondary-text-color-1;
			}
		}
		.thumbs-updown {
			width: 32px;
			height: 32px;
			border: 1px solid $border-dark-color;
			border-radius: $radius;
			margin-left: 7px;
			padding: 5px;
			background-color: $app-white;
			cursor: pointer;
			&:hover {
				background-color: $app-sidebar-bg;
			}
		}
		.helpful {
			background-color: $app-jade-light;
			border: 1px solid $hummingBirdGreenBorder;
			.upvote path{
				fill: $alertSuccessColor;
			}
		}
		.not-helpful {
			background-color: $alertWarningBackground;
			border: 1px solid $alertWarningBorder;
			.downvote path {
				fill: $btnDangerBorder;
			}
		}
		.disable {
			pointer-events: none;
		}
	}
	.cue-tickets {
		margin-top: 24px;
		h2 {
			font-size: $title-small;
			font-weight: $text-weight-medium;
			line-height: 16.71px;
			padding: 0 0 2px 32px;
		}
		// related to tabs 
		.cue-ticket-view {
			padding-left: 32px;
			box-shadow: 0 1px 6px rgba(22,29,37,.05);
			li {
				margin-right: 40px;
				a {
					padding: 0;
				}
			}
			
		}
		.cue-ticket-list-table {
			padding: 0 32px;
			.ember-light-table {
				border: none;
				tr :first-child{
					&.lt-column, &.lt-cell {
						width: 550px;
					}
				}
				.lt-body-wrap thead.lt-head th.lt-column {
					height: 32px;
					font-size: $text-normal;
					font-weight: $text-weight-medium;
					border-bottom: none;
					&:first-child {
						border-radius: 4px 0 0 4px;
						border-right: 1px solid $app-border-primary;
					}
					&:last-child {
						border-radius: 0 4px 4px 0;
					}
				}

				.lt-body-wrap tbody.lt-body {
					.show-ellipsis {
						width: 323px;
						display: inline-block;
						white-space: nowrap;
						text-overflow: ellipsis;
						overflow: hidden;
					}
					.display-id{
						position: absolute;
						padding-left: 3px;
					}
				}

				.no-records {
					width: 270px;
					height: 270px;
					margin: 45px auto 27px auto;
				}
				.no-records-txt {
					color: $app-form-label-color;
					font-size: $title-small;
					line-height: 18px;
				} 
			}
		}
		.ember-pagination-new {
			border-top: 0;
			.pagination-wrapper {
				justify-content: center;
			}
		}
	}
}

.ember-modal-dialog.ember-modal-confirm.cue-helpful-modal {
	width: 512px;
	.ember-modal-header {
		font-weight: $text-weight-semi-bold;
		font-size: $text-large;
		line-height: 27px;
		padding-bottom: 24px;
	}
	.ember-modal-body {
		.comments {
			margin-bottom: 8px;
		}
		.feedback{
			height: 88px;
			width: 100%;
			margin-top: 8px;
		}
	}
}
.resolve-cue-modal{
	.common-label {
		font-size: $text-normal;
		font-weight: $text-weight-semi-bold;
		color: $app-form-label-color;
	}
	.assign_group{
		margin: 0 0 16px;
		label{
			@extend .common-label;
		}
		#assign-group-element span{
			font-size: $baseFontSize;
		}
	}
	.assign_agent{
		margin: 0 0 16px;
		label{
			@extend .common-label;
		}
		#assign-agent-element span{
			font-size: $baseFontSize;
		}
	}
	.cue-solution-article {
		label {
			padding-bottom: 8px;
			@extend .common-label;
		}
		.article-list {
			padding-top: 24px;
			.section-keyword {
				border: 1px solid $app-border-primary;
				padding: 20px 12px;
				border-radius: $radius;
				display: flex;
				margin-bottom: 8px;
				.file {
					width: 32px;
					height: 32px;
					border: 1px solid $border-light-blue;
					background: $section-background-blue;
					border-radius: $radius;
					display: flex;
					align-items: center;
					.img-file {
						margin: 0 auto;
						height: 16px;
						width: 13px;
					}
				}
				.article-title {
					display: flex;
					align-items: center;
					width: 326px;
					padding: 0 12px;
					font-weight: $text-weight-medium;
				}
				.add-keywords {
					height: 32px;
					visibility: hidden;
					background: $white;
					border: 1px solid $btnBorder;
					border-radius: $radius;
					display: flex;
					color: $btnPrimaryBorder;
					align-items: center;
					font-weight: $text-weight-semi-bold;
					cursor: pointer;
					margin-left: $page-margin-left;
					svg  {
						width: 14px;
						margin: 8px 8px 8px 2px;
					}
				}
				.pending-approval {
					background: $app-casablanca-light;
					border: 1px solid $alertPartialBorder;
					border-radius: $radius;
					padding: 5px 8px;
					font-size: $text-normal;
					color: $alertPartialColor;
					display: flex;
					font-weight: $text-weight-semi-bold;
					align-items: center;
				}
				.pending-approval-icon {
					display: flex;
					margin-left: 8px;
					align-items: center;
					fill: $app-form-label-color;
					cursor: pointer;
					.info-tooltip-container {
						left: -82px !important;
						top: -8px !important;	
						padding: 6px 12px;
					}
					svg {
						width: 20px;
						height: 20px;
					}
				}

				.show-pending-approvals {
					height: 30px;
					display: flex;
					visibility: hidden;
				}

				&:hover {
					background-color: $background-color-2;
					.add-keywords, .show-pending-approvals {
						visibility: visible;
					}
				}
			}
		}
		.cue-article-search-field {
			position: relative;
			width: 100%;
			input {
				padding: 0 32px !important;
				font-weight: $text-weight-normal !important;
				width: inherit;
			}
			svg {
				position: absolute;
				margin: 9px 12px;
			}
		}
		.search-article {
			text-align: center;
			.empty-state-for-articles {
				height: 210px;
				margin-top: 46px;
			}
			.search-article-title {				
				margin-top: 28px;
				font-weight: $text-weight-normal;
				line-height: 21px;
				color: $inputHoverBorder;
				font-size: $title-small;
			}
		}
		.show-article-keywords {
			border: 1px solid $inputFocusBorder;
			box-sizing: border-box;
			border-radius: $radius;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
			.top-section {
				display: flex;
				padding: 16px;
				.file {
					width: 32px;
					height: 32px;
					margin-right: 12px;
					border: 1px solid $border-light-blue;
					background: $section-background-blue;
					border-radius: $radius;
					display: flex;
					align-items: center;
					.img-file {
						fill: $svg-icon-blue-color;
						height: 16px;
						width: 16px;
						margin: 8px;
					}
				}
				.article-title {
					display: flex;
					align-items: center;
					font-weight: $text-weight-medium;
				}
			}
			.ember-basic-dropdown {
				.ember-power-select-trigger {
					padding: 12px 16px 32px;
					border-radius: 0 0 4px 4px !important;
					.ember-power-select-multiple-options {
						.ember-power-select-multiple-option  {
							padding-left: 8px;
							margin-bottom: 8px;
						}
						.highlight-cue {
							background: $app-jade-light;
							border-radius: $radius;
							color: $alertSuccessColor;
							.ember-power-select-multiple-remove-btn {
								fill: $alertSuccessColor;
							}
						}
					}
					.ember-power-select-trigger-multiple-input {
						margin-left: 8px;
						width: auto;
						padding: 0px;
					}
					.ember-power-select-status-icon {
						display: none;
					}
				}
				.ember-basic-dropdown-content-wormhole-origin {
					display: none;
				}
			}
				
		}
		.bottom-section {
			display: flex;
			margin-top: 10px;
			justify-content: space-between;
			.view-article {
				color: $app-primary;
				fill: $app-primary;
				font-weight: $text-weight-semi-bold;
				line-height: 20px;
				display: flex;
				align-items: center;
				.view-article-title {
					margin-right: 8px;
				}
				.open-new-tab {
					height: 13px;
				}
			}
			.submit-keywords {
				.btn-primary {
					margin-right: 0;
				}
			}
		}

	}
	.ember-modal-header {
		padding: 32px 32px 24px !important;
		.title {
			line-height: 32px;
		}
		.resolve-cue-sub-title {
			padding-top: 8px;
			line-height: 21px;
		}
		.resolve-cue-warning-title {
			background: $app-casablanca-light;
			padding: 8px;
			margin-top: 8px;
			line-height: 21px;
			border: 1px solid $alertPartialBorder;
			border-radius: $radius;
			display: flex;
			justify-content: space-between;
			height: 42px;
			.alert-icon {
				fill: $app-red;
				cursor: pointer;
				display: flex;
				padding-right: 7px;
				border-right: 1px solid $alertPartialBorder;
				svg {
					width: 20px;
				}
			}
			.alert-content {
				flex: auto;
				padding-left: 10px;
			}
			.alert-close {
				padding: 0px 12px;
				font-size: $text-large;
				line-height: 24px;
				font-weight: $text-weight-medium;
				color: $app-primary-black;
				border: none;
				background: none;
				cursor: pointer;
				:not(:hover) {
					opacity: .5;
				}
			}
		}
	}
	.ember-modal-body {
		.solutions_text {
			z-index: 0;
		}
		.cue-solution-article {
			.empty-state-for-articles {
				margin-top: 46px;
			}
		}
	}
	.fr-element{
		padding: 0 !important;
		margin: 10px !important;
	}
	.fr-placeholder{
		display: none;
	}
}