.show-parent {
	position: fixed;
	left: 50%;
	top: 25%;
	z-index: 1000;
	width: 375px;
	height: 550px;

	.resizers {
		width: 100%;
		height: 100%;
		display: flex;
		flex-direction: column;

		.resizer {
			position: absolute;
			height: 20px;
			width: 20px;
		}

		.bottom-right {
			right: -5px;
			bottom: -5px;
			cursor: nwse-resize;

			.app-icon {
				fill: $secondary-text-color-1;
			}
		}
	} 

	&-container {
		border-radius: 5px 5px 0 0;
		background-color: $app-light-bg;
		height: 100%;

		@extend .default-box-shadow;
	}

	&-header {
		height: 50px;
		background-color: $primary-text-color-1;
		display: flex;
		justify-content: space-between;
		align-items: center;
		color: $app-light-bg;
		padding: 16px;
		border-radius: 5px 5px 0 0;
		position: absolute;
		width: 100%;
		top: 0;
		box-sizing: border-box;

		p {
			color: $app-light-bg;
			margin: 0;
			display: flex;
		}

		.action--close {
			width: 20px;
			height: 20px;
			cursor: pointer;
			fill: $app-light-bg;
		}

		.app-icon {
			fill: $app-light-bg;
			height: 20px;
			width: 20px;
			margin-right: 16px;
		}
	}

	&-content {
		padding: 8px 16px;
		max-height: calc(100% - 50px);
		height: calc(100% - 50px);
		overflow: auto;
		margin-top: 50px;
		box-sizing: border-box;

		.text-title{
			font-size: $title-extra-large;
			font-weight: $text-weight-semi-bold;
		}

		hr.line {
			margin-top: 4px;
			margin-bottom: 8px;
			width: 100%;
		}

		&-description.article-body,
		&-description {
			width: auto !important; /* stylelint-disable-line */
		}

		&-description img,
		.fr-video > * {
			box-sizing: content-box;
			max-width: 100%;
			border: none;
		}

		&-description p,
		&-description li {
			margin: 0;
		}
	}
}