.article-insights, .article-approvals {
	min-height: 650px;
	& > h3 {
		font-size: $text-large;
		font-weight: 500;
	}

	& > p {
		font-size: $title-small;
		color: $app-form-label-color;
		margin-bottom: 16px;
	}

	.ui-list .ui-list-item {
		& > a {
			color: $primary-text-color-1;
			display: flex;
			align-items: center;
			width: 100%;
			height: 100%;
		}

		.insight-relationship-info, .approval-relationship-info {
			padding-top: 4px;
			@extend .ellipsis;

			div > span {
				display: inline-block;
				color: $secondary-text-color-1;
				font-size: $text-normal;
				max-width: 45%;
				@extend .ellipsis;
			}
		}
	}
}

.article-insights {
	.ui-list-item > a {
		justify-content: space-between;
	}

	.insight-relationship-info {
		width: 90%;
	}
}

.article-approvals {
	padding: 16px 24px;
	.ui-list-item {
		display: flex;
		align-items: center;

		& > a {
			width: 90% !important;
			.approval-relationship-info {
				width: 70%;
				margin-right: 16px;
			}

			.approval-article-owner {
				display: flex;
				span:nth-child(2) {
					font-weight: $text-weight-semi-bold;
					display: inline-block;
					max-width: 100px;
					text-overflow: ellipsis;
					white-space: nowrap;
					overflow: hidden;
					padding-left: 4px;
				}
			}
		}

		.approval-button {
			width: 10%;
			text-align: right;
		}
	}
}
