.module-solutions {
	.ember-light-table{
		margin: 0 8px;
		width: calc(100% - 16px) !important;
		/* scrollable table has bug in edge due to border radius */
		border-radius: 0;

		.cell-article-header-title {
			border-right: 1px solid $border-dark-color;
		}
		.cell-article-item-title {
			a {
				margin-left: 8px;
				color: $primary-text-color-1;
				display: block;
				cursor: pointer;
				&:hover {
					color: $linkColor;
				}
			}
		}
	}

	.articles-table {
		$table-row-types: 'header', 'item';
		$table-columns: 'title', 'status', 'thumbs-up', 'thumbs-down', 'views', 'inserted-into-tickets', 'author-name', 'folder-name', 'category-name', 'translate';
		$table-columns-width: 300px, 150px, 120px, 120px, 100px, 180px, 150px, 200px, 200px, 150px;

		@each $type in $table-row-types {
			@each $col in $table-columns {
				$i: index($table-columns, $col);

				.cell-article-#{$type}-#{$col} {
					width: nth($table-columns-width, $i);
					.disabled-translate {
						color: $disabled-text-color;
						cursor: default;
					}
				}
			}
		}

		.ember-light-table {
			margin-bottom: 8px;
		}

		.table-wrap {
			margin-top: 0;
		}

		.articles-bulk-actions {
			border-top: 1px solid #cfd7df;
			display: flex;
			align-items: center;
			min-height: 48px;
			padding: 0 16px;

			&-options {
				display: inline-block;
			}

			[type="checkbox"] {
				margin-right: 8px !important;
				margin-left: 8px;
			}

			label {
				padding-bottom: 0;
			}

			.articles-bulk-actions-options {
				padding-left: 8px;
			}
		}

	}
}
