.module-solutions {

	&.new-article-form-page,
	&.edit-article-form-page {
		.page-title {
			#article-form-buttons{
				margin-left: auto;
			}
		}

		.page-content {
			.article-status {
				margin: 8px;
				.remove-alert-divider {
					&:before {
						content: none;
					}
				}
				.current-language {
					display: flex;
					align-items: center;
					margin-right: 8px;

					.language-flags {
						margin-right: 4px;
						border-radius: 4px;
					}
				}
				.show-parent-block {
					margin-left: auto;
					font-size: $title-small;
					border: 0;
					outline: none;
				}
			}
			
			border: 2px solid transparent;

			&.focus {
				border-color: $linkColor;
			}

			.article-form {
				padding: 16px;

				.form-field--label {
					display: none !important;
				}

				.text-field {
					input {
						border: none;
						font-size: $title-extra-large;
						font-weight: $text-weight-medium;
						box-shadow: none !important;

						&::placeholder {
							color: $disabled-text-color;
						}
					}

					.form-field--errors {
						padding-left: 8px;
					}
				}

				.textarea-field {
					margin: 8px;
					.froala-editor-container {
						border: 0;
						padding-top: 8px;
						.fr-toolbar {
							border: none;
							box-shadow: none;
							background-color: $button-hover-color;
							border-radius: 0;

							.fr-newline {
								background: none;
							}
						}
						.fr-placeholder {
							margin-left: 0 !important;
							margin-right: 0 !important;
						}

						.fr-wrapper {
							box-shadow: none;
							border: none;
							.fr-element { margin: 0 !important; }
						}

						.second-toolbar {
							border: none;
						}
					}
				}
			}
		}

		.page-sidebar {
			.ember-form{
				padding: 16px;
			}
		}
	}

	.external-article-custom{
		padding-top: 10px;
		.basic-dropdown-item{
			min-width: 200px;
		}
		&:before{
			content: "";
			width: 95%;
			position: absolute;
			border-top: 1px solid $app-footer-border-top;
			margin-left: -3px;
		}
	}
}
