.page-sidebar-open{
	.sidebar-toggle{
		background-color: $background-color;
	}
}

#solution-modules-dropdown-wrapper {
	background: $background-color;
	display: flex;

	.solution-modules-dropdown, .language-selection-dropdown {
		height: 50px
	}

	> :first-child {
		margin-left: auto;
	}
	> :nth-child(2) {
		margin-left: 8px;
	}
}

.solution-modules-dropdown {

	.ember-basic-dropdown-trigger[aria-expanded="true"] {
		transition: all 0.2s ease;
		.btn svg {
			transform: rotate(-180deg);
		}
	}

	.btn{
		margin: 0 0 0 4px;
		display: flex;
		align-items: center;

		svg {
			width: 16px;
			height: 16px;
			fill: #fff;
			margin-left: 4px;
		}
	}

	.ember-basic-dropdown-content{
		min-width: 140px;
	}
}

.language-selection-dropdown {
	.ember-power-select-trigger {
		padding: 0 12px;
		margin-top: 0;
		height: 32px;
		min-width: 90px;

		> div {
			height: 30px;
			display: flex;
			align-items: center;

			span {
				padding-left: 4px;
			}
		}
	}

	.ember-basic-dropdown-content {
		width: 264px !important;
		.ember-power-select-options {
			padding: 8px;
		}
		.ember-power-select-option {
			margin: 4px 0;
			padding: 8px 30px 8px 8px;
		}
	}

	.language {
		&-flags {
			border-radius: 4px;
			display: inline-block;
			vertical-align: middle;
		}

		&-name {
			display: inline-block;
			vertical-align: middle;
		}
	}
}

.module-solutions {
	.page-title {
		box-sizing: border-box;

		.ember-breadcrumbs {
			.sidebar-toggle {
				width: 32px;
				height: 32px;
				border-radius: 4px;
				text-align: center;
				padding: 8px;
				box-sizing: border-box;

				svg {
					stroke: $primary-text-color-1;
					width: 14px;
					height: 14px;
				}
			}
		}
	}

	&.kbase-index-page {
		.page-content {
			min-height: 100%;
			height: 100%;
			position: relative;
		}

		.page-sidebar {
			height: 100vh;
			box-shadow: 2px 0 4px rgba(0, 0, 0, 0.1);
		}

		.page-title .ember-breadcrumbs{
			margin-left: inherit !important;
		}
		&.is-sticky {
			.sticky-element-container+.page-content{
				margin-left: 316px;
			}
		}
	}

	.page-content {
		background-color: $app-white;
		.insights-title {
			padding: 16px 20px 12px;
			font-weight: 500;
			font-size: 18px;
			line-height: 20px;
		}
		.articles-header {
			box-sizing: border-box;

			.articles-relationship-wrapper {
				position: relative;

				.folder-description, .category-description {
					font-size: 14px;
					margin-bottom: 8px;
					color: $secondary-text-color-1;
					overflow-wrap: break-word;
					max-width: 80%;
				}

				.folder-visibility, .folder-managed-by-groups {
					font-size: 12px;
					margin-top: 4px;
					&-label{
						color: $secondary-text-color-1;
					}

					&-value{
						color: $secondary-text-color-1;
						font-weight: 500;
					}
				}

				.articles-relationship-actions{
					position: absolute;
					top: 16px;
					right: 32px;
					display: inline-flex;
					.folder-actions .ember-basic-dropdown-content {
						min-width: 150px;
					}
				}

				.articles-relationship-title {
					font-size: 18px;
					max-width: 80%;
					font-weight: 500;
					display: inline-block;
					color: $primary-text-color-1;

					&:nth-child(2) {
						margin-right: 8px;
					}
				}

				.category-edit,
				.category-delete {
					svg {
						width: 16px;
						height: 16px;
						cursor: pointer;
						fill: $primary-text-color-1;
					}
				}
			}

			.articles-filter {
				&-dropdown {
					&-title {
						display: flex;
						font-size: 18px;
						font-weight: 500;
						cursor: pointer;

						svg {
							width: 20px;
							height: 20px;
							margin-left: 4px;
						}
					}

					.ember-basic-dropdown-trigger[aria-expanded="true"] {
						svg {
							transform: rotate(-180deg);
						}
					}

					.applied-filter {
						padding-right: 30px;
						svg {
							width: 20px;
							height: 20px;
							visibility: hidden;
							float: right;
							margin-top: 6px;
						}

						&.active {
							svg {
								visibility: visible;
							}
						}
					}

					.ember-basic-dropdown-content{
						min-width: 140px;
					}
				}

				&-helptext {
					font-size: 12px;
					color: $secondary-text-color-1;
				}
			}

			&>div:first-child {
				padding: 16px 24px;
			}
		}
		.solution-insights{
			border-style: none;
			.tabs__items {
				border-bottom: 1px solid $border-light-color;
			}
			#knowledge-cue{
				padding: 16px;
				.cue-info{
					margin: 8px;
					display: flex;
					.info-text{
						font-size: 12px;
						line-height: 20px;
						color: $app-form-label-color;
						margin-right: 12px;
						a {
							font-weight: $text-weight-bold;
						}
					}
					.refresh-date{
						display: flex;
						align-items: center;
						padding: 12px;
						border-left: 1px solid $app-border-primary;
						svg{
							margin-left: 4px;
							margin-top: -1px;
							width: 14px;
							height: 14px;
							fill: $app-grey;
							outline: none;
							cursor: pointer;
						}
						.info-tooltip-container{
							cursor: pointer;
							padding: 8px;
							left: -64px !important;
						}
						.refresh-date-title {
							width: 205px;
							font-size: 12px;
							color: $primary-text-color-1;
							font-weight: 600;
						}
					}
				}
				.cue_views{
					margin: 16px 8px;
					font-weight: $text-weight-semi-bold;
					svg{
						vertical-align: middle;
						width: 20px;
						height: 20px;
					}
					.ember-basic-dropdown-trigger {
						cursor: pointer;
						display: inline;
					}
					.ember-basic-dropdown-content {
						width: 200px;
					}
					.basic-dropdown-item.active {
						color: $inputFocusBorder;
						font-weight: $text-weight-semi-bold;
					}
				}
				.cues-insights{
					position: relative;
					.table-loader{
						position: absolute;
						z-index: 999;
						width: 100%;
						top: 32px;
						.shimmer-loader-item{
							margin: 34px;
						}
					}
					.cues-table{
						.mention-cell{
							svg{
								margin: 0 8px;
								vertical-align: middle;
							}
							.feedback{
								font-size: 12px;
								color: $secondary-text-color-1;
								margin-left: 80px;
								&.helpful{
									path{
										fill: $alertSuccessColor;
									}
								}
								&.unhelpful{
									path{
										fill: $btnDangerBorder;
									}
								}
							}
						}
						.ember-light-table {
							border: none;
							.cues-header {
								border-right: 1px solid $border-dark-color;
							}
							table thead.lt-head .lt-column {
								font-size: $text-normal;
								border-bottom: none;
								height: 40px;
							}
							
						}
					}
					.ember-pagination-new {
						border-top: none;
						.pagination-wrapper {
							justify-content: center;
						}
					}
				}
			}
		}
	}

	.page-sidebar {
		background-color: $app-white;
		overflow-y: auto;

		.solutions-sub-modules {
			padding: 8px 4px 4px 8px;
			border-bottom: 1px solid $app-border-primary;

			.sub-module-all-articles, .sub-module-approvals, .sub-module-insights {
				height: 32px;
				line-height: 32px;
				display: block;
				border-radius: 4px;
				padding: 0 12px;
				cursor: pointer;

				span {
					color: $primary-text-color-1;
				}

				svg {
					fill: $svg-icon-color;
					width: 16px;
					height: 16px;
					position: relative;
					top: 3px;
					pointer-events: none;
					margin: 0 8px;
				}

				&.active {
					background-color: $app-primary;

					span {
						color: $app-white;
					}

					svg {
						fill: $app-white;
					}
				}
				.cue-refresh-notification {
					margin: 0px;
					height: 14px;
					width: 14px;
					padding-bottom: 2px;
				}
			}
			.show-indicator {
				span:after {
					content: '';
					display: inline-block;
					position: relative;
					width: 8px;
					height: 8px;
					border-radius: 50%;
					background-color: $app-warning-state;
					left: 3px;
					top: -2px;
				}
			}
		}
	}
	.empty-state {
		min-height: 650px;

		> p {
			margin: 8px 0;
		}

		.empty-state-for-solutions{
			.category { margin-left: -15px; }
			.folder { margin-left: 5px; }
			.article, .no-approvals, .no-analytics { margin-left: -20px; }
		}
	}
}

.folder-modal,
.category-modal {
	.ember-modal-body {
		.select-field.form-field{
			width: 300px;
		}
	}

	.secondary-language-warning {
		line-height: 20px;
		height: auto;
		padding: 8px 16px;
	}

	.disabled-category {
		.ember-basic-dropdown {
			pointer-events: none;
			opacity: 0.6;
		}
	}

	.folder-approver-ids label{
		display: none !important;
	}

	.folder-approver-ids, .folder-approval-type {
		& + p {
			margin-top: -12px;
		}
	}

	textarea {
		resize: vertical !important;
		max-height: 200px;
	}
}

.external-article-edit-modal, .external-article-create-modal, .external-article-show-modal {
	.ember-modal-header{
		.ember-alert{
			margin: 24px 0px 8px 0px;
		}
	}
	.ember-modal-body {
		/* adjusting height as 40px is occupied by the banner */
		height: calc(100% - 205px) !important;
		.article-card{
			padding: 0px;
			box-shadow: none;
			label:before{
				content: "";
				padding-top: 32px;
				display: inline-block;
			}
			span.ember-pill{
				font-weight: 500;
			}
		}
	}
	.ember-modal-footer-custom{
		.ember-modal-footer{
			justify-content: normal !important;
			display: block !important;
			.external-url-custom-footer{
				display: flex;
				justify-content: space-between;
				padding: 8px;
			}
		}
	}
}

.folder-delete-confirm-modal,
.category-delete-confirm-modal,
.article-delete-confirm-modal,
.solution-delete-confirm-modal {
	.ember-modal-header {
		padding: 24px 32px 0 32px !important;
		.title {
			svg {
				fill: $primary-text-color-1;
				width: 20px;
				height: 20px;
				margin-right: 8px;
			}

			color: $primary-text-color-1;
			font-weight: 600;
		}
	}

	.ember-modal-body {
		padding: 4px 32px 0 !important;
		max-height:  160px !important;
		.form-field--label {
			display: none !important;
		}

		.warning-info {
			font-weight: $text-weight-semi-bold;
			color: $primary-text-color-1;
		}
	}
}

.ember-alert-warning {
	border: 1px solid $alertWarningBorder;
	background-color: $alertWarningBackground;
	border-radius: 5px;
	padding: 8px !important;
	svg {
		fill: $alertWarningColor;
		border-right: 1px solid $alertWarningBorder;
		width: 30px;
	}
}

.solution-change-author-modal,
.solution-change-status-modal, 
.solution-change-folder-modal {
	.ember-modal-footer{
		width: auto !important;
	}
	.ember-modal-body {
		.ember-alert.ember-alert-warning {
			padding: 24px 16px;
			font-size: $text-normal;
			margin-bottom: 16px;
		}
	}
}

.article-reorder-modal{
	.info {
		margin-top: 10px;
	}
	.article-list {
		.ember-alert {
			border: 1px solid $primary-border;
			background-color: $inputBackground;
			min-height: 36px;
			height: auto;
			margin-top: 8px;
			padding: 4px;
			color: $primary-text-color-1;
			svg{
				fill: $primary-text-color-1;
			}
		}

		.infinity-loader.reached-infinity span {
			display: none;
		}

		.dragSortItem.-placeholderAfter:before,
		.dragSortItem.-placeholderBefore:before {
			display: none;
		}
	}
}

.article-show-requested-edits-modal {
	.ember-modal-body{
		max-height: 200px !important;
		overflow-y: auto;
	}
}
