// Article Table Overrides

// thumb-down column
:lang(ca) {
	.module-solutions .articles-table {
		.cell-article-item-thumbs-down, .cell-article-header-thumbs-down {
			width: 160px;
		}
	}
}

// views column
:lang(ca), :lang(it) {
	.module-solutions .articles-table {
		.cell-article-item-views, .cell-article-header-views {
			width: 160px;
		}
	}
}

:lang(ar), :lang(he) {
	.language-selection-dropdown {
		.ember-basic-dropdown-content {
			left: auto;
			right: 0;
		}
	}
}
