.article-search-textbox {
	background-color: $dropdown-hover;
	position: absolute;
	width: 70%;
	padding-bottom: 10px;
	z-index: 1;
	top: 0;

	.search-icon {
		height: 15px;
		padding: 9px;
		position: absolute;
	}

	.close-icon {
		position: relative;
		cursor: pointer;
		right: 25px;
		top: 5px;
		svg {
			height: 20px;
		}
	}

	svg {
		opacity: 0.3;
		width: 20px;
		pointer-events: none;
	}

	.search-text-box {
		border: 1px solid $primary-border;
		padding: 6px 20px 8px 32px;
		width: 60%;
		&.focus {
			border: 2px solid $linkColor;
		}
		&::-ms-clear {  display: none; width : 0; height: 0; }
		&::-ms-reveal {  display: none; width : 0; height: 0; }
	}

	.ember-loader {
		position: relative;
		top: 5px;
		right: 25px;

		& + .close-icon {
			display: none;
		}
	}

	.search-results {
		position: absolute;
		border: 1px solid $primary-border;
		border-radius: 5px;
		width: 60%;
		background-color: $app-white;
		box-sizing: border-box;
		max-height: 300px;
		overflow: auto;

		li {
			padding: 4px 8px;
			&:hover {
				a {
					background-color: $dropdown-option-selected;
				}
			}
		}

		.search-result {
			color: inherit;
			padding: 4px 8px;
			display: block;
			.search-result-child {
				display: block;
			}
		}
	}
}
